*{
    background-color: rgb(26, 26, 29);
}

#modeSelector{
text-align: center;
height: 450px;
width: 400px;
background-color:rgb(29, 28, 20) ;
border-radius: 25px;
position: absolute;
top: 50%;
margin-left: 50%;
margin-right: 50%;
transform: translate(-50%,-50%);
}




#multiplayer:active{
    border: 1px solid red;
}
#twoplayer:active{
    border: 1px solid red;
}
#singleplayer:active{
    border: 1px solid red;
}

#multiplayer{
    height: 25%;
    margin-bottom: 3px;
    border-radius: 5px ;
    border: 1px solid black;
    width: 50%;
    background-color:rgb(14, 14, 14) ;
    margin-left: 50%;
    margin-right: 50%;
    transform: translate(-50%,0);
}

#singleplayer{
    height: 25%;
    margin-bottom: 3px;
    border-radius: 5px ;
    border: 1px solid black;
    width: 50%;
    background-color:rgb(14, 14, 14) ;
    margin-left: 50%;
    margin-right: 50%;
    transform: translate(-50%,0);
}
#twoplayer{
    height: 25%;
